.header{
  width: 100%;
  height:1.1rem;
  background: #D43D3D;
  font-size: 0.5rem;
  line-height: 1.1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left:0.5rem;
  padding-right:0.5rem;
  color: #FFFFFF;
  position: fixed;
  z-index: 999;
}
.header img{height: 1rem;
}
.header .cent{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nav{
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  margin-top: 1.1rem;
  background: #F4F5F6;
    font-size: 0.4rem;
    position: fixed;
    z-index: 999;overflow: hidden;
}
.img img{position: relative;z-index: 9999;left: 15rem;top: 1.2rem;
}
.nav .dh{
  margin-left:1rem;
}
.footer{width: 100%;height:1.6rem;color: #fff;background: rgba(0,0,0,0.8);position: fixed;bottom: 0;z-index: 999;
}
.footer .left{display: -webkit-box;display: -ms-flexbox;display: flex;margin-left: 0.5rem;margin-top: 0.3rem;font-size: 0.3rem;
}
.right{display:-webkit-box;display:-ms-flexbox;display:flex;margin-top: 0.3rem;margin-right: 0.65rem;
}
.open{background: #FF444C;width: 3rem;height: 1rem;margin-right: 0.2rem;border-radius: 5px;text-align: center;line-height:1rem;
}
.open a{color: #fff;
}
.btn{font-size: 1rem;line-height: 0.9rem;
}



